.right {
  float: right; }

.nomargin {
  margin: 0 !important; }

.nopadding {
  padding: 0 !important; }

.hidden {
  display: none; }

.drag {
  position: absolute;
  padding: 5px 10px 5px 10px;
  border: 1px solid #666;
  background: #eee;
  width: 160px; }



