.iteminfo {
  padding: 0;

  font-size: 90%;
  line-height: 11px;
  text-align: left;

  .image {
    text-align: right;
    height: 170px;

    .align {
      display: inline-block;
      height: 100%;
      vertical-align: middle;
      a, img {
        vertical-align: middle;
        width: 170px;
        max-height: 170px; } }

    img {
      width: 170px;
      max-height: 170px; } }

  .rating {
    margin-top: 4px; }

  .container_content {

    dl {
      padding: 0;
      list-style-type: none;
      width: 100%;
      margin: 0 0 7px;

      &.contact {
        div p {
          line-height: 140%;
          padding: 0 0 11px !important; } }

      span.tag {
        margin: 2px 2px 0 0;
        padding: 0 4px;
        background: #333;
        color: white;
        border: 1px solid #333;
        text-transform: uppercase; }

      &.license {
        p {
          padding: 4px 0 0 0 !important; }
        img {
          height: 10px; } }

      dd.relations {

        li {
          padding: 0 3px 0 0;
          margin: 0 3px 0 0;
          float: left;
          span.service {
            display: none; }
          a {
            color: #5a5a5a;
            text-decoration: none;
            &:hover {
              color: $primary-color-b; }
            i {
              font-size: 180%;
              line-height: 17px; } } } }


      &.links.external {

        dd {
          float: none; } } }

    li {
      padding: 0;
      margin: 0; }

    p {
      color: #666666;
      &.label {
        width: 70px;
        color: #000000; } } }


  .container_image {
    padding: 14px;
    a {
      display: block;
      border: 1px solid #F5F5F5;
      &:hover {
        border: 1px solid $primary-color-a; } } }

  .item_menu {
    //margin-bottom: 20px
    h3 {
      margin: 0;
      padding: 0;
      text-transform: uppercase;
      font-size: 140%; }
    ul {
      margin: 0px; } } }




// analyse display
.item_detail {
  section.analyse, section.file-properties {
    // background: #ff0000
    table {
      thead tr {
        padding-bottom: 1px;
        th {
          // font-weight: normal
          text-align: left; } }
      tbody {
        td.value {
 }          // width: 100px
        td.bar {
          width: 120px;
          .holder {
            margin: 4px 10px 0 0;
            background: #efefef;
            border: 1px solid #cdcdcd;
            .indicator {
              height: 10px;
              background-color: #cdcdcd;
              position: relative;
              span {
                font-weight: bold;
                position: absolute;
                top: 2px;
                left: 3px;
                font-size: 9px;
                line-height: 9px; } } } } } } }


  section.statistics {
    .choices-container {
      ul {
        margin-top: 10px;
        clear: both;
        > li {
          padding: 0 30px 0 0;
          float: left;
          input, label {
            float: left; }
          label {
            padding-left: 10px; } } } } } }








.item_menu {
  padding: 0px;
  margin: 0px;
  background-color: $body-color-shaded-bg;
  height: auto;
  float: left;
  width: 100%;
  font-size: 90%;
  ul {
    line-height: 1em;
    list-style: none outside none;
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;
    float: left;
    bottom: -1px;
    margin-left: 0px; }
  li {
    float: left;
    border: none;
    padding: 0;
    margin: 0 0 -1px 10px;
    &.spacer {
      border: none;
      border-top: 1px solid #F5F5F5;
      border-bottom: 1px solid #bbb;
      padding: 5px; }
    &.on, &.active {
      border-bottom: 1px solid $body-color-bg;
      a {
        //border-bottom: #fff
        background-color: $body-color-bg;
 } }        //color: $primary-color-a

    a {
      background: none repeat scroll 0 0 $body-color-bg;
      color: #a5a5a5;
      display: block;
      margin: 0;
      text-decoration: none;
      font-size: $toolbar-font-size;
      padding: 2px 10px;
      &.on, &.active {
        border-bottom: #fff;
        background-color: #fff;
        color: $primary-color-a; }
      &.disabled {
        color: #c1c1c1;
        cursor: wait; }
      &:hover {
        border-bottom: none;
        background-color: #fff;
        color: $primary-color-a; }
      &.disabled:hover {
        border-bottom: none;
        background-color: #e1e1e1;
        color: #c1c1c1; } } } }


div.item_inner {
  div.container {
    &.lyrics {
      font-size: 115%;
      line-height: 190%;
      font-style: italic;

      p {
        margin-bottom: 14px; } } } }




/* specific items, maybe move to other files some time.. */
section.item_inner .tab-pane {
  section.profile {
    &.information {
      h4 {
        text-transform: none;
        color: #a5a5a5; }
      dl {
        margin: 4px 0 8px; } } } }


// relations in sidebar
.sidebar {

  .listing {
    &.relations {

      .social {
        height: 24px;
        margin: 2px 0 8px; }

      .relations > ul {
        li {
          cursor: pointer;
          padding: 4px 0 6px 0;
          border-bottom: 1px solid #efefef;
          clear: both;
          a {
            color: #5a5a5a;
            &:hover {
              text-decoration: none; } }

          i {
            font-size: 160%;
            width: 40px;
            display: block;
            float: left; }
          span.service {
            top: 3px;
            position: relative;
            display: block;
            width: 100%; }

          &:hover a {
            color: $primary-color-b; } } } } } }

