// THUMBNAILS
// ----------

.thumbnails {
  margin-left: -$gridGutterWidth;
  list-style: none;
  @include clearfix; }

.thumbnails > li {
  float: left;
  margin: 0 0 $baseLineHeight $gridGutterWidth; }

.thumbnail {
  display: block;
  padding: 0px;
  line-height: 1;
  border: 1px solid #ddd;
  @include border-radius($baseRadiusSmall);
  @include box-shadow(0 1px 1px rgba(0, 0, 0, 0.075)); }

// Add a hover state for linked versions only
a.thumbnail:hover {
  border-color: $linkColor;
  @include box-shadow(0 1px 4px rgba(0, 105, 214, 0.25)); }

// Images and captions
.thumbnail > img {
  display: block;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto; }

.thumbnail .caption {
  padding: 9px; }
