@charset "UTF-8";

body.home {
  margin-top: $toolbar-height !important; }

$h1-font-size: 36px;
$h2-line-height: 40px;
$h2-font-weight: 600;

%typography-base {
  font-family: 'Helvetica Neue','Source Sans Pro', sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 1px; }

%typography-teaser {
  h1, h2 {
    margin: 0 !important; }
  h1 {
    font-weight: 600;
    font-size: 42px;
    line-height: 48px; }
  h2 {
    font-weight: 300;
    font-size: 42px;
    line-height: 48px; } }


///////////////////////////////////////////////////////////////
// teaser styles
///////////////////////////////////////////////////////////////
body.home {
  section.teaser {
    position: relative;
    .teaser-container {
      height: 400px; }

    .background {
      height: 400px;
      background: url(/static/img/asset/home.teaser.bg.jpg);
      background-repeat: no-repeat;
      background-size: 100% 400px; }

    .text {
      text-align: center;
      position: absolute;
      top: 0;
      width: 100%;
      padding: 50px 0 0 0;

      @extend %typography-teaser;
      h1, h2 {
        color: white; } } } }


///////////////////////////////////////////////////////////////
// frontpage search
///////////////////////////////////////////////////////////////
body.home {
